body{
  background-color: hsl(0, 100%, 74%);
  background-image: url(/src/images/bg-intro-desktop.png);
}


main {
  margin: 10% 25%;
  width: 50vw;
  height: 50vh;
  display: grid;
  position: absolute;
  grid-template-columns: 1fr 1fr;
  column-gap: 50px;
  justify-content: space-around;

}

.content{
margin-top: 6%;
}

.content h2{

  font-size: 50px;
  color: white;
  font-weight: 500;
  
}

.content p{
 color: white;
 line-height: 30px;
 font-size: 20px;
}

form{
  background-color:white;
    border-radius: 10px;
    padding-left: 30px;
    padding-top: 50px;
    padding-bottom: 15px;
    padding-right: 55px;
    text-align: center;

}

.box{
  margin-left: 5%;
}


.box button{
  background-color: green;
    color: rgb(255, 255, 255);
    border-radius: 6px;
    padding: 20px 126px;
    border: none;
    font-size: 11px;

}

.box input{
  border-radius: 6px;
  padding: 16px 120px;
  border-color: whitesmoke;
  border-style: solid;
  text-align: center;
  font-family: 'Poppins', sans-serif;
}

.box p{
  background-color: hsl(250, 46%, 43%) ;
    border-radius: 10px;
    padding: 15px 30px;
    color: white;
    font-size: 12px;
    text-align: center;
}

.box h6{
  font-size: 10px;
  color: rgb(192, 192, 192);
}

